import React, {Component} from  'react'
import $ from 'jquery'
import url from './audio/gbqq.mp3'


class Main extends Component{
    state={
        date:{},
    }
    componentDidMount(){
        this.print();
        setInterval(()=>{
                this.time(2018,12,17)
            },1000
        )
        var audio = document.getElementById("audio");
        setTimeout(()=>audio.play(),8500)
    }
    print = ()=>{
        $.fn.autotype = function() {
            var _this=$(this);
            var str=_this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str=str.replace(/(\s){2,}/g,"$1");
            var index = 0;
            $(this).html('');
            var timer = function fn() {
                var args=arguments;
                var current = str.slice(index, index+1);
                // html标签完整输出,如：<p>
                if (current == '<'){
                    index = str.indexOf('>', index) + 1;
                }
                else{
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length-1){ //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                }else{
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                };
                setTimeout(fn,200)
            };
            // 延迟1s开始
            setTimeout(timer,1000);
        };
        $("#autotype").autotype();
    }
    time =(year,month,day)=>{
        var dateNow = new Date();
        var dateJNR = new Date(year,month-1,day);
        // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
        var d = parseInt((dateNow - dateJNR)/(24*3600*1000));
        var hour = parseInt(((dateNow - dateJNR)/(3600*1000))%24);
        var minute = parseInt((dateNow - dateJNR)/(1000*60)%60);
        var second = parseInt((dateNow - dateJNR)/1000%60);
        this.setState({date:{d:d,hour:hour,minute:minute,second:second}});
    };
    render(){
        const date =()=>{
            if (this.state.date.d!==undefined){
                const {d,hour,minute,second} = this.state.date
                return (<p>我们已经相识: <span className="date-text">{d}</span> 天 <span className="date-text">{hour}</span> 小时 <span className="date-text">{minute}</span> 分 <span className="date-text">{second}</span> 秒 </p>
                )
            }
        }
        return(
            <div className="App animated bounceInLeft">
            <div className="date">{date()}</div>
            <div id="autotype">
                <h1 style={{fontWeight:900}}>嗯嗯~  猫猫！</h1>
                <p>在煽情开始之前，先放首歌当背景音乐吧！Music!</p>
                <p>这里我有些话想和你说。其实那晚过后我挺后悔的，因为那些话，我自己都不知道我自己在说什么，想表达什么。</p>
                <p>我不怎么擅长表达自己，哪怕到要和你表白这么重要的时刻我也说不出什么话，我也只能说我喜欢你，想和你在一起。这就是我心里最真切的想法，我也不知道用什么话去修饰。</p>
                <p>我只想和你简简单单地，陪你聊天，陪你开心，陪你玩。我会努力，努力做到你心中想要的那个样子。</p>
                <p>给我个机会，慢慢地了解彼此，我不会对你有所隐瞒，可能以后我会让你伤心，让你难过，但我一定会认错，永远向你低头，不会背叛你。我会给你安全感，给你浪漫，不会像现在这么这么直男。</p>
                <p>这些已经是我目前能想到的，但是我还有很多很多话想对你说，细水长流，可以给我个机会和你慢慢说吗？</p>
                <div style={{textAlign:'right'}}>
                    <p>爱你的♥魏锐</p>
                    <p>2018年4月23日</p>
                </div>
            </div>
                <audio id="audio" src={url}></audio>
            </div>

        )
    }
}
export default Main



